<template>
  <loadingTip :active="isLoading"></loadingTip>
  <div class="text-end mt-3">
    <button class="btn btn-primary" type="button" @click="openModal(true)">
    
    </button>
  </div>
  <table class="table mt-4">
    <thead>
      <tr>
        <th width="120"></th>
        <th></th>
        <th width="120"></th>
        <th width="120"></th>
        <th width="100"></th>
        <th width="200"></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="item in products" :key="item.id">
        <td>{{item.category}}</td>
        <td>{{item.title}}</td>
        <td class="text-right">
          {{item.origin_price}}
        </td>
        <td class="text-right">
          {{item.price}}
        </td>
        <td>
          <span class="text-success" v-if="item.is_enabled == 1 || item.enable == 1"></span>
          <span class="text-muted" v-else></span>
        </td>
        <td>
          <div class="btn-group">
            <button
              class="btn btn-outline-primary btn-sm"
              @click="openModal(false, item)"
            ></button>
            <button class="btn btn-outline-danger btn-sm" @click="openDelProductModal(item)"></button>
          </div>
        </td>
      </tr>
    </tbody>
  </table>
  <!-- props 
    :product="tempProduct"
    ProductModalprops= data
   -->
  <PageView
    :page="pagination"
    @emit-page="getProducts"
  ></PageView>
  <ProductModal
    @update-product="updateProduct"
    ref="productModal"
    :product="tempProduct"
  >
  </ProductModal>
  <DelModal :item="tempProduct" ref="delModal" @del-item="delProduct"/>
</template>

<script>
// 
import ProductModal from '../components/ProductModal.vue'
import DelModal from '@/components/DelModal.vue'
import PageView from '@/components/PageView.vue'

export default {
  data () {
    return {
      products: [], // 
      pagination: {}, // (propsPageView)
      tempProduct: {}, // 
      isNew: false, // (true)or(false)
      isLoading: false // 
    }
  },
  components: {
    //  :()
    ProductModal,
    DelModal,
    PageView
  },
  inject: ['emitter'],
  methods: {
    // 
    // (page = 1) page1
    getProducts (page = 1) {
      const api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/products/?page=${page}`
      this.isLoading = true
      this.$http.get(api)
        .then((res) => {
          if (res.data.success) {
            console.log(res)
            this.products = res.data.products // dataproducts
            this.pagination = res.data.pagination // datapagination
            this.isLoading = false
          }
        })
    },
    // /
    openModal (isNew, item) {
      console.log(isNew, item)
      if (isNew) {
        // (true)
        this.tempProduct = {}
      } else {
        // (false)
        this.tempProduct = { ...item }
      }
      this.isNew = isNew
      const productComponent = this.$refs.productModal
      productComponent.showModal() // 
    },

    // /
    updateProduct (item) {
      // emititemtempProduct
      this.tempProduct = item

      // api
      let api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product`
      let httpMethod = 'post'

      // isNewapi
      if (!this.isNew) {
        api = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${item.id}`
        httpMethod = 'put'
      }

      // 
      const productComponent = this.$refs.productModal
      this.isLoading = true
      productComponent.hideModal() // 
      this.$http[httpMethod](api, { data: this.tempProduct })
        .then((res) => {
          console.log(res) // 
          this.getProducts() // 
          this.isLoading = false

          // mitt ToastMessage
          if (res.data.success) {
            this.getProducts()
            this.emitter.emit('push-message', {
              style: 'success',
              title: ''
            })
          } else {
            this.emitter.emit('push-message', {
              style: 'danger',
              title: '',
              content: res.data.message.join('')
            })
          }
        })
    },

    //  Modal
    openDelProductModal (item) {
      this.tempProduct = { ...item } // (delProduct()id)
      const delComponent = this.$refs.delModal
      delComponent.showModal()
    },

    // 
    delProduct () {
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/product/${this.tempProduct.id}`
      this.isLoading = true
      this.$http.delete(url)
        .then((response) => {
          console.log(response.data)
          const delComponent = this.$refs.delModal
          delComponent.hideModal()
          this.getProducts()
          this.isLoading = false
          if (response.data.success) {
            this.emitter.emit('push-message', {
              style: 'success',
              title: ''
            })
          } else {
            this.emitter.emit('push-message', {
              style: 'danger',
              title: ''
            })
          }
        })
    }
  },
  created () {
    this.getProducts()
  }

}
</script>
