<template>
  <!-- Modal -->
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="modal"
  >

    <!--  v-model -->
    <div class="modal-dialog modal-xl" role="document">
      <div class="modal-content border-0">
        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title" id="exampleModalLabel">
            <span></span>
          </h5>
          <button type="button" class="btn-close"
                  data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body">
          <div class="row">
            <div class="col-sm-4">
              <div class="mb-3">
                <label for="image" class="form-label"></label>
                <input
                  v-model="tempProduct.imgLink"
                  type="text"
                  class="form-control"
                  id="image"
                  placeholder=""
                  >
              </div>
              <div class="mb-3">
                <label for="customFile" class="form-label"> 
                  <i class="fas fa-spinner fa-spin"></i>
                </label>
                <input
                  type="file"
                  id="customFile"
                  class="form-control"
                  ref="fileInput"
                  @change="uploadFile"
                >
              </div>
              <img :src="tempProduct.imgLink" class="img-fluid" alt="">

            <!--  -->
            <div class="mt-5" v-if="tempProduct.images">
              <label class="form-label"></label>
              <div v-for="(image, key) in tempProduct.images" :key="key" class="mb-3 input-group">
                <input
                  type="url"
                  class="form-control form-control"
                  placeholder=""
                  v-model="tempProduct.images[key]"
                  >
                <button
                  type="button"
                  class="btn btn-outline-danger"
                  @click="tempProduct.images.splice(key, 1)"
                >
                  
                </button>
              </div>
                <!--  -->
                <!-- 1. (='' false) -->
                <!-- 2. (=0 false truev-if) -->
                <!-- 3. 5 -->
              <div v-if="tempProduct.images[tempProduct.images.length - 1] &&tempProduct.images.length <= 4 || !tempProduct.images.length ">
                <button
                  class="btn btn-outline-primary btn-sm d-block w-100"
                  @click="tempProduct.images.push('')"
                >
                  
                </button>
              </div>
            </div>

            </div>
            <div class="col-sm-8">
              <div class="mb-3">
                <label for="title" class="form-label"></label>
                <input
                  v-model="tempProduct.title"
                  type="text"
                  class="form-control"
                  id="title"
                  placeholder=""
                >
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="category" class="form-label"></label>
                  <input
                    v-model="tempProduct.category"
                    type="text"
                    class="form-control"
                    id="category"
                    placeholder=""
                  >
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input
                    v-model="tempProduct.unit"
                    type="text"
                    class="form-control"
                    id="unit"
                    placeholder=""
                  >
                </div>
              </div>

              <div class="row gx-2">
                <div class="mb-3 col-md-6">
                  <label for="origin_price" class="form-label"></label>
                  <input
                    v-model="tempProduct.origin_price"
                    type="number"
                    class="form-control"
                    id="origin_price"
                    placeholder=""
                  >
                </div>
                <div class="mb-3 col-md-6">
                  <label for="price" class="form-label"></label>
                  <input
                    v-model="tempProduct.price"
                    type="number"
                    class="form-control"
                    id="price"
                    placeholder=""
                  >
                </div>
              </div>
              <hr>

              <div class="mb-3">
                <label for="description" class="form-label"></label>
                <textarea
                  v-model="tempProduct.description"
                  type="text"
                  class="form-control"
                  id="description"
                  placeholder=""
                  >
                </textarea>
              </div>
              <div class="mb-3">
                <label for="content" class="form-label"></label>
                <textarea
                  v-model="tempProduct.content"
                  type="text"
                  class="form-control"
                  id="content"
                  placeholder=""
                  >
                </textarea>
              </div>
              <div class="mb-3">
                <div class="form-check">
                  <input
                    v-model="tempProduct.enable"
                    class="form-check-input"
                    type="checkbox"
                    :true-value="1"
                    :false-value="0"
                    id="is_enabled"
                  >
                  <label class="form-check-label" for="is_enabled">
                    
                  </label>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary"
                  data-bs-dismiss="modal">
          </button>
          <button
            @click="$emit('update-product',tempProduct)"
            type="button"
            class="btn btn-primary"
          >
          
          </button>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import modalMixin from '@/mixins/modalMixin'

export default {
  props: {
    product: {
      type: Object, // 
      default () { return {} } // 
    }
  },

  watch: {
    // propsproducttempProduct
    product () {
      this.tempProduct = this.product
      console.log('this.tempProduct.images' + this.tempProduct.images)
      if (!this.tempProduct.images) {
        this.tempProduct.images = [] // object truev-if
      }
    }
  },
  // data
  data () {
    return {
      modal: {},
      tempProduct: {} // (propstempProduct)
    }
  },

  // 
  methods: {
    // 
    uploadFile () {
      const uploadedFile = this.$refs.fileInput.files[0]
      console.dir(uploadedFile) // dir?
      const formData = new FormData() // apiform
      formData.append('file-to-upload', uploadedFile) // api
      const url = `${process.env.VUE_APP_API}api/${process.env.VUE_APP_PATH}/admin/upload`
      const select = this.$refs.fileInput.id // input id
      this.$http.post(url, formData)
        .then((res) => {
          console.log(res.data)
          if (res.data.success) {
            this.tempProduct.imgLink = res.data.imageUrl // data
            document.getElementById(select).value = '' // input value
          }
        })
    }
  },
  mixins: [modalMixin]
}
</script>
